body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
nav {
  background: rgba(0, 0, 0, 0.7);
  border-bottom: #000 2px solid;
}

.navbar .nav-link {
  font-size: 1rem;
  text-transform: uppercase;
  padding: 0 1rem !important;
}

.navbar .nav-item.active {
  border-left: #444 3px solid;
}

.carousel-item {
  height: 450px;
}

.dark-overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 0;
  margin: 0;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.carousel-item {
  height: 100vh;
}
.carousel-caption {
  padding-bottom: 80px;
}

.carousel-image-1 {
  background: url('./img/cover3.jpg');
  background-size: cover;
}

.carousel-image-2 {
  background: url('./img/cover4.jpg');
  background-size: cover;
}

#home-heading-section,
#person-heading-section {
  position: relative;
  background: url('./img/lights.jpg');
  background-size: cover;
  min-height: 250px;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

#gallery-heading-section,
#display-heading-section {
  position: relative;
  background: url('./img/media.jpg');
  min-height: 250px;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

#gallery-heading-section::before,
#home-heading-section::before,
#person-heading-section::before,
#display-heading-section::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.home-heading-caption {
  z-index: 1000 !important;
}

#home-icon a {
  text-decoration: none;
  color: #000;
}

#main-footer {
  background: #000;
  color: #fff;
}

.img-wrap {
  position: relative;
}

#person img {
  position: relative;
}

#person a,
#gallery-caption-mb a {
  text-decoration: none;
  color: #fff;
}

.card-columns img {
  max-height: 500px !important;
}

.card-columns img {
  width: auto !important;
  max-width: 355px;
  height: 100% !important;
}
.card-img-wrap {
  height: 300px !important;
}

#gallery-caption-mb .card-body {
  background: #444;
}

#person .circle-dark-overlay {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  z-index: 20;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transition: 500ms all;
  -o-transition: 500ms all;
  transition: 500ms all;
}

#person .circle-dark-overlay:hover {
  background: rgba(0, 0, 0, 0.8);
  opacity: 1;
}

#person .rounded-circle {
  background: #444;
  z-index: 10;
  width: 100%;
  height: 100%;
}

.card-columns img {
  max-height: 200px;
}

.bg-gallery-dark {
  background: #222;
}
.gallery-mt {
  margin-top: 58px;
  z-index: 100;
}

.gallery-imgs img {
  width: 100% !important;
}

.gallery-caption {
  -webkit-box-shadow: 0px 6px 15px #000;
          box-shadow: 0px 6px 15px #000;
  position: relative;
}

#PersonNavName a {
  text-decoration: none;
}

.text-name-muted {
  color: #6c757d !important;
}

.bg-mb-light {
  background: rgb(145, 145, 145) !important;
}
.text-black {
  color: #000;
}

.navbar-brand {
  font-size: 1rem;
  color: #ddd !important;
}

.img-info {
  -webkit-transition: 500ms all;
  -o-transition: 500ms all;
  transition: 500ms all;

  opacity: 0;
}
.img-info-border {
  border-top: #fff 2px solid;
  border-bottom: #fff 2px solid;
}

.img-info:hover {
  opacity: 1;
}

@media (max-width: 768px) {
  .btn-mb-block {
    display: block;
    width: 100%;
  }
  .carousel-image-1 {
    background: url('./img/cover-mb.jpg');
    background-size: cover;
    background-position: center;
  }
  .carousel-image-2 {
    background: url('./img/cover4-mb.png');
    background-size: cover;
    background-position: center;
  }
  .carousel-item h1 {
    font-size: 3rem;
  }

  #home-heading-sectionï¼Œ #gallery-heading-section {
    background: url('./img/lights.jpg');
  }
  #person .circle-dark-overlay {
    border-radius: 0 0 279px 279px;
    height: 50%;
    top: 50%;
    opacity: 1;
    background: rgba(0, 0, 0, 0.5);
  }

  #gallery-caption-pc {
    display: none !important;
  }
  h1 {
    font-size: 2rem !important;
  }

  .display-mb-margin {
    padding-left: 0 !important;
    margin-left: 0 !important;
    text-align: center;
  }
}

@media (min-width: 769px) {
  #gallery-caption-mb {
    display: none !important;
  }
}



